.button {
  cursor: pointer;

  &-wraper {
    @include pageGrid;
  }

  display: flex;

  height: 56px;
  padding: 0 48px;

  justify-content: center;
  align-items: center;

  border-radius: 8px;
  background: #f98921;

  color: #fff;
  text-decoration: none;
  font-weight: 700;
  line-height: 100%;

  grid-column: 1 / -1;

  &__categories {
    @include onDesktop {
      grid-column: span 4;
    }
  }

  &__contact-us {
    border: 0;
    outline: 0;
    margin-top: 16px;
    margin-bottom: 80px;

    @include onDesktop {
      grid-column: 1 / 7;
      margin-bottom: 0;
    }
  }

  &:hover {
    background-color: #1b2129;
    color: #fff;
  }

  &:active {
    background-color: #010810;
    color: #fff;
  }
}

.photo-link__button {
  @include hover(transform, scale(1.07));
}
